/* Style for Tabs Section */
#projects {
    padding: 40px 20px;
    margin: 1.2em 0 5em 0;
  }
#projects h3 {
  text-decoration: underline;
}
  .tabs {
    display: flex;
    margin-bottom: 20px;
    flex-direction: column;
    width: max-content;
    margin: auto;
  }
  
  .tab-button {
    background-color: #f0f8ff;
    color: #333;
    padding: 12px 20px;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
    flex-grow: 1;
    text-align: center;
    border: 2px solid black;
    margin-top: 0.5em;
    width: max-content;
    margin: auto;
    border-radius: 4px;
    margin-bottom: 0.5em;
    max-width: 80%;
  }
  
  .tab-button.active {
    background-color: #0073e6;
    color: #080808;
  }
  
  .tab-button:hover {
    background-color: #2aacbd;
  }
  
  /* Content Style */
  .tab-content {
    display: none;
  }
  
  .tab-content.active {
    display: block;
  }
  
  h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  ul {
    font-size: 1rem;
    margin-left: 20px;
  }
  
  a {
    color: #0073e6;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  iframe {
    width: 100%;
    height: 456px;
    border: none;
  }

  .my-icon {
    background-repeat: no-repeat;
    background-size: contain;
    width: 48px;
    height: 48px;
    margin: auto;
  }
  .github-icon {
    background-image: url("/public/github.png");
  }
  .playstore-icon {
    background-image: url("/public/playstore.png");

  }
.screenshots {
  display: flex;
  flex-direction: column;
  gap: 10px; /* space between screenshots */
}

.screenshot {
  width: 100%;
  height: 200px; /* adjust to whatever you need */
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 8px;
  border: 1px solid black;
}

  .embedIframe {
    width: 100%;
    height: 700px;
  }

  .brackets {

    top: -100%;
  }

  li:nth-child(even) {
    color: #260eb1;
  }
  @media (min-width: 600px) {

    .screenshot {
      height: 500px; /* adjust to whatever you need */
    }
  }
  @media (min-width: 1200px) {
    #projects {
      max-width: 1200px;
      margin: auto;
      margin-bottom: 5em;
    }
  }